.card {
    position: relative;
    transform-origin: top;
    animation: newgame 0.5s ease;
}

@keyframes newgame {
    from {
        transform: scaleY(0);
    }
    to {
        transform: scaleY(1);
    }
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* front of card - the picture */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
    cursor: pointer;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}
